@import url(https://fonts.googleapis.com/css2?family=Fira+Sans:wght@300;400&family=Montserray:wght@400;500;600;700&display=swap);
.header .navbar{padding:0 !important;background-color:#0a0a0a}.header .navbar a{color:#fbfbfc !important;padding:1.8rem !important;position:relative}.header .navbar #nav-icon1{margin-right:8px}.header .navbar .collapsing{height:auto !important}.header .navbar .nav-link::after{content:'';position:absolute;width:0px;height:1px;left:50%;bottom:0;background-color:#fbfbfc;transition:all ease-in-out 0.3s}.header .navbar .nav-link:hover::after{width:100%;left:0}@media (max-width: 767.98px){.header .navbar{padding:1em !important}.header .navbar #nav-icon1{margin-right:1rem}.navbar-collapse{background-color:#0a0a0a;text-align:center;font-size:1.5rem;opacity:0.9}.navbar-collapse .nav-link{padding-bottom:100%}}

.landing ul{padding:0}.connect-list{display:flex;justify-content:space-around;margin-bottom:1.5em}.connect-list li{text-align:center;padding:1em 2.5em}@media (max-width: 575.98px){.connect-list li{text-align:center;padding:1em 0.8em}}@media only screen and (min-device-width: 320px) and (max-device-width: 568px) and (-webkit-min-device-pixel-ratio: 2) and (orientation: landscape){.connect-list{margin-bottom:.5em}.connect-list li{text-align:center;padding:1em 0.5em}}

.connect-icon{position:relative;display:flex;justify-content:center;align-items:center;background-color:#fff;height:3rem;width:3rem;border-radius:20%;font-size:2rem;outline:0 solid;text-decoration:none !important;box-shadow:0 0.25em 0.25em rgba(0,0,0,0.3);transition:all 0.3s ease-out}.connect-icon svg:hover{color:white}.connect-icon:hover,.connect-icon:focus{background-color:currentColor !important;outline:0 solid;-webkit-transform:scale(1.12);transform:scale(1.12);box-shadow:0 0.5em 0.5em rgba(0,0,0,0.4)}.connect-icon:hover::before,.connect-icon:focus::before{opacity:1;-webkit-transform:translate(-50%, -70%) scale(1);transform:translate(-50%, -70%) scale(1)}.connect-icon::before{content:attr(aria-label);position:absolute;left:50%;bottom:-100%;font-size:0.8rem;white-space:nowrap;color:white;background-color:#3b2d4a;padding:0em 0.7em;margin:0 0 0.8em 0;box-shadow:0 0.5em 0.5em rgba(0,0,0,0.4);opacity:0;-webkit-transform:translate(-50%, -180%) scale(0.7);transform:translate(-50%, -180%) scale(0.7);border-radius:2em;transition:0.3s;pointer-events:none;cursor:default}.connect-icon .svg-icon{padding:10px;width:100%;height:100%}.cv{font-size:0.9em !important}@-webkit-keyframes pressed-social{0%{-webkit-transform:scale(1.1);transform:scale(1.1)}50%{-webkit-transform:scale(0.8);transform:scale(0.8);background-color:#3b2d4a;box-shadow:0 0.5em 0.5em rgba(0,0,0,0)}}@keyframes pressed-social{0%{-webkit-transform:scale(1.1);transform:scale(1.1)}50%{-webkit-transform:scale(0.8);transform:scale(0.8);background-color:#3b2d4a;box-shadow:0 0.5em 0.5em rgba(0,0,0,0)}}.file,.github{color:#000 !important}.fa-linkedin:active{color:white}.linkedin,.mail{color:#007bb5 !important}@media only screen and (min-device-width: 320px) and (max-device-width: 568px) and (-webkit-min-device-pixel-ratio: 2) and (orientation: landscape){.connect-icon{position:relative;display:flex;justify-content:center;align-items:center;background-color:#fff;height:2rem;width:2rem;border-radius:20%;font-size:1rem;outline:0 solid;text-decoration:none !important;box-shadow:0 0.25em 0.25em rgba(0,0,0,0.3);transition:all 0.3s ease-out}.connect-icon svg:hover{color:white}.connect-icon .svg-icon{padding:5px;width:100%;height:100%}}

