.card{background-color:#f0f0f0 !important}.card .project-img{width:100%;padding:1em 1em}.card .card-subtitle{margin-top:1em !important;margin-bottom:0.5em !important}.card .card-text:last-child{margin-bottom:1rem !important}.card .used-tech-container{padding-bottom:0 !important;padding-top:0 !important}.card .used-tech{list-style:none;padding-top:10px}.card .used-tech .p-languages{margin:0.5rem auto;font-size:14px;background:#536cdf;color:#fff;display:inline-block;padding:0.1rem 1rem;border-radius:5px}.card .used-tech .p-libraries{margin:0.5rem auto;font-size:14px;background:#bb2d40;color:#fff;display:inline-block;padding:0.1rem 1rem;border-radius:5px}.card .used-tech li{padding-right:10px}.card .custom-btn{background-color:#0f0f0f !important;color:white;border-radius:4px !important;display:inline-block !important;transition:0.5s !important;letter-spacing:1px}.card .custom-btn:hover{box-shadow:rgba(0,0,0,0.22) 0px 19px 43px;-webkit-transform:translate3d(0, -1px, 0);transform:translate3d(0, -1px, 0);background-color:#fbfcfb !important;color:#0a0a0a !important}


