*{box-sizing:border-box}html{height:100%}body{font-family:'Fira Sans', sans-serif !important;height:100%}.title-container{padding-top:8em}a{text-decoration:none !important}ul,li{list-style:none;text-decoration:none}.landing{background:#0a0a0a;height:100%;background-position:center;background-repeat:no-repeat;background-size:cover;background-attachment:fixed}.landing .title{font-size:4.5rem;font-weight:600;font-family:'Montserrat', sans-serif;color:#fbfbfc;margin-bottom:1rem}.landing .subtitle-typed{font-size:1.5rem;letter-spacing:5px;color:#fbfbfc}.landing .separator{background-color:#fbfbfc;width:25%;margin-top:1rem;margin-bottom:1.5rem}.landing .view-project-btn-container .custom-btn{margin-top:15px;padding:8px 20px;background:#3498db !important;background-color:#dd006f !important;color:white;border-radius:4px !important;margin-top:50px;margin:0 10px;display:inline-block !important;transition:0.5s !important;letter-spacing:1px}.landing .view-project-btn-container .custom-btn:hover{box-shadow:rgba(0,0,0,0.22) 0px 19px 43px;-webkit-transform:translate3d(0, -1px, 0);transform:translate3d(0, -1px, 0);background-color:#ff80bf !important;color:#350a1f !important}@media (max-width: 575.98px){.landing .title{text-align:center;font-size:3.5rem;font-weight:600;font-family:'Montserrat', sans-serif;color:#fbfbfc;margin-bottom:1rem}.landing .subtitle-typed{font-size:1rem;letter-spacing:5px;color:#fbfbfc}}

