.connect-icon {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #fff;
  height: 3rem;
  width: 3rem;
  border-radius: 20%;
  font-size: 2rem;
  outline: 0 solid;
  text-decoration: none !important;
  box-shadow: 0 0.25em 0.25em rgba(0, 0, 0, 0.3);
  transition: all 0.3s ease-out;

  svg:hover {
    color: white;
  }

  &:hover,
  &:focus {
    background-color: currentColor !important;
    outline: 0 solid;
    transform: scale(1.12);
    box-shadow: 0 0.5em 0.5em rgba(0, 0, 0, 0.4);
  }
  &:hover::before,
  &:focus::before {
    opacity: 1;
    transform: translate(-50%, -70%) scale(1);
  }

  &::before {
    content: attr(aria-label);
    position: absolute;
    left: 50%;
    bottom: -100%;
    font-size: 0.8rem;
    white-space: nowrap;
    color: white;
    background-color: #3b2d4a;
    /* Less accessible option: background-color: inherit;*/
    padding: 0em 0.7em;
    margin: 0 0 0.8em 0;
    box-shadow: 0 0.5em 0.5em rgba(0, 0, 0, 0.4);
    opacity: 0;
    transform: translate(-50%, -180%) scale(0.7);
    border-radius: 2em;
    transition: 0.3s;

    pointer-events: none;
    cursor: default;
  }

  .svg-icon {
    padding: 10px;
    width: 100%;
    height: 100%;
  }
}

.cv {
  font-size: 0.9em !important;
}

@keyframes pressed-social {
  0% {
    transform: scale(1.1);
  }

  50% {
    transform: scale(0.8);
    background-color: #3b2d4a;
    box-shadow: 0 0.5em 0.5em rgba(0, 0, 0, 0);
  }
}

.file,
.github {
  color: #000 !important;
}

.fa-linkedin:active {
  color: white;
}

.linkedin,
.mail {
  color: #007bb5 !important;
}

@media only screen and (min-device-width: 320px) and (max-device-width: 568px) and (-webkit-min-device-pixel-ratio: 2) and (orientation: landscape) {
  .connect-icon {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #fff;
    height: 2rem;
    width: 2rem;
    border-radius: 20%;
    font-size: 1rem;
    outline: 0 solid;
    text-decoration: none !important;
    box-shadow: 0 0.25em 0.25em rgba(0, 0, 0, 0.3);
    transition: all 0.3s ease-out;

    svg:hover {
      color: white;
    }

    .svg-icon {
      padding: 5px;
      width: 100%;
      height: 100%;
    }
  }
}
